.quiz-header{
    background-color: black;
    height: 10%;
    width: 100%;
    padding: 1%;
    text-align: left;
    padding-left: 10%;
    margin-bottom: 2%;
    display: flex;
}

.heading{
    margin-left: 1%;
    font-weight: bolder;
    cursor: pointer;
}

.user{
    text-align: right;
    position: relative;
    left: 60%;
    cursor: pointer;
}

.wel-msg{
    text-align: left;
    width: 50%;
    padding: 1%;
    margin-left: 2%;
    font-size: 50px;
    color: aquamarine;
    font-family: cursive;
    font-weight: bold;
}

.detl-msg{
    padding: 1%;
    margin-top: 2%;
    color: rgb(201, 247, 231);
    font-family: fantasy;
    font-weight: bolder;
}

.form{
    margin: 1%;
    padding: 2%;
    margin-top: 4%;
    color: rgb(232, 250, 244);
    font-family: monospace;


}

.user-name{
    font-size: 25px;
    margin-right: 2%;
}

#username{
    width: 30%;
    padding: .5%;
    border-radius: 9px;

}

.container{
    display: flex;
    margin: auto;
    margin-top: 3%;
    justify-content: center;
}

.select-btn{
    margin: 1%;
    flex: 1;
    position: relative;
    left: 1%;
}

.categry{
    display: inline-block;
    margin: 1%;
    flex: 1;
    font-size: 20px;
    position: relative;
    right: 20%;
}

.btn-manul{
    margin: 4%;
}


.result-container{
    width: 80%;
    margin: auto;
}

.greet{
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    margin: 4%;
    font-size: 400%;
    color: rgb(21, 255, 255);
}

.show-results{
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    margin: 4%;
    margin-top: 10%;
    font-size: 200%;
    color: rgb(198, 248, 248);
}






.quiz-footer{
    background-color: black;
    width: 100%;
    padding: 1%;
    text-align: left;
    padding-left: 10%;
    position: fixed;
    bottom: 0;
    left: 0;

}